export const validateEmail = (value) => {
  let error;
  if (!value) {
    error = ' email ';
  } else if (!/^[a-z0-9](\.?[a-z0-9]){5,}@(emailsoldiers|creativesoldiers|leadplan)\.(ru|com)$/g.test(value)) {
    error = '   @emailsoldiers, @creativesoldiers, @leadplan';
  }
  return error;
};

